.header {
  position: relative;
  min-height: 80px;
}
.header__logo {
  margin-top: 20px;
  margin-left: -30px;
}
.header__menu {
  margin-top: 35px;
  margin-left: -25px;
}
.logo {
  display: block;
  color: #ec4848;
  font-size: 33px;
  font-weight: bold;
  text-transform: uppercase;
  font-family: 'PT Sans', sans-serif;
}
.logo:hover,
.logo:focus {
  color: #ec4848;
  text-decoration: none;
}
.menu {
  position: relative;
  margin-right: 30px;
  display: inline-block;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 0;
  z-index: 10;
  transition: all 0.35s ease;
}
.menu__item {
  margin-right: 32px;
  color: #3f444a;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
}
.menu__item:last-child {
  margin-right: 0;
}
.menu__item--active,
.menu__item--active:focus,
.menu__item--active:hover {
  color: #ec4848;
}
.search {
  position: absolute;
  top: 30px;
  right: 0;
}
.search__input {
  margin-right: 30px;
  padding-left: 15px;
  padding-right: 15px;
  height: 25px;
  width: 0;
  border: 1px solid #000;
  border-radius: 10px;
  outline: none;
  visibility: hidden;
  transition: all 0.35s ease;
}
.search__input:focus {
  border-color: #ec4848;
}
.search__button {
  float: right;
  margin-left: auto;
  padding: 0;
  width: 30px;
  height: 30px;
  background-color: transparent;
  background-image: url("../../images/header/header__search.jpg");
  border: none;
  outline: none;
}
